import React, { Component, CSSProperties } from 'react';

export interface IFormInputProps {
  id: string;
  label: string;
  isRequired: boolean;
}

export class FormInput extends Component<IFormInputProps> {
  public render() {
    const formInputCss: CSSProperties = {
      paddingBlockEnd: '10px',
      textAlign: 'left',
      paddingLeft: '15%'
    };

    const blackText: CSSProperties = {
      color: '#353535',
      paddingBottom: '10px',
      fontSize: 20,
    };

    const grayText: CSSProperties = {
      color: '#808080',
    };

    const inputCss: CSSProperties = {
      height: '30px',
      width: '400px',
      fontSize: 20,
      paddingLeft: '10px',
      paddingTop: '10px',
      paddingBottom: '10px',
      border: '1px solid #eee',
      borderRadius: '3px',
      background: '#fafafa',
    };

    const { id, label, isRequired } = this.props;

    return (
      <div id={id} style={formInputCss}>
        <div>
          <label htmlFor={id} style={blackText}>
            <b>{label}</b>
            {isRequired && <label style={grayText}> (required)</label>}
          </label>
        </div>
        <div>
          <input name={id} type="text" style={inputCss} />
        </div>
      </div>
    );
  }
}
