import React, { Component, CSSProperties } from 'react';
import { FormInput } from './FormInput';
import { PageTitle } from '../PageTitle';

export class ContactPage extends Component {
  public render() {
    const css: CSSProperties = {
      backgroundColor: 'white',
      width: '100%',
      paddingBlockEnd: '5%'
    };

    const grayText: CSSProperties = {
      color: '#353535',
      paddingBlockEnd: '10px',
      textAlign: 'left',
      paddingLeft: '15%',
      paddingRight: '15%',
      fontSize: 20
    };

    return (
      <div id="contact-page" style={css}>
        <PageTitle title="Contact" />
        <p style={grayText}>
          This is a contact page with some basic contact information and a
          contact form.
        </p>
        <FormInput id="name" label="Name" isRequired={true} />
        <FormInput id="email" label="Email" isRequired={true} />
        <FormInput id="website" label="Website" isRequired={false} />
        <FormInput id="comment" label="Comment" isRequired={true} />
      </div>
    );
  }
}
