import React, { Component, CSSProperties } from 'react';
import image from '../assets/MainTitleImage.jpeg';

export class MainTitleImage extends Component {
  public render() {
    const css: CSSProperties = {
      paddingTop: '50px'
    };

    return (
      <img
        id="main-title-image"
        style={css}
        src={image}
        alt="Main Title Rags Renewal"
      />
    );
  }
}
