import React, { Component, CSSProperties } from 'react';
import { PageTitle } from '../PageTitle';

export class AboutPage extends Component {
  public render() {
    const css: CSSProperties = {
      backgroundColor: 'white',
      width: '100%',
      paddingBlockEnd: '5%'
    };

    const grayText: CSSProperties = {
      color: '#353535',
      paddingBlockEnd: '10px',
      textAlign: 'left',
      paddingLeft: '15%',
      paddingRight: '15%',
      fontSize: 20
    };

    return (
      <div id="contact-page" style={css}>
        <PageTitle title="About" />
        <p style={grayText}>
          I began documenting my fashion journey in early 2013 when I made a
          YouTube account. I created videos on how to re-use items of clothing
          as well as vintage hauls and DIY fashion projects. I gradually stopped
          making the videos and joined Instagram and have posted my outfits ever
          since. In the past I have dabbled in the blogging world but now I want
          to focus more on it, combining my loves of fashion, art and music.{' '}
          <br />
          <br />
          All my drawings and photographs are my own unless otherwise stated.
          <br />
          <br />
          Manchester based.
        </p>
      </div>
    );
  }
}
