import React, { Component, CSSProperties } from 'react';
import { MainTitleImage } from './MainTitleImage';

export class Title extends Component {
  public render() {
    const grayText: CSSProperties = {
      color: '#808080'
    };

    return (
      <div id="main-title">
        <MainTitleImage />
        <h1>Rags Renewal</h1>
        <p style={grayText}>
          Northern twenty-something with a love for vintage and high street
          fashion
        </p>
      </div>
    );
  }
}
