import React, { Component, CSSProperties } from 'react';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import { ContactPage } from './contact/ContactPage';
import { AboutPage } from './about/AboutPage';

export class Navigation extends Component {
  public render() {
    const tabCss: CSSProperties = {
      width: '200px',
      height: '60px',
      background: '#191919',
      textAlign: 'center',
      lineHeight: '60px',
      
      marginBottom: '5px'
    };

    const spanCss: CSSProperties = {
      display: 'inline-block',
      lineHeight: 'normal'
    };

    return (
      <Router>
        <nav id="navigation">
          <ul>
            <Link to="/">
              <div id="home-link" style={tabCss}>
                <span style={spanCss}>Home</span>
              </div>
            </Link>
            <Link to="/about/">
              <div id="about-link" style={tabCss}>
                <span style={spanCss}>About</span>
              </div>
            </Link>
            <Link to="/contact/">
              <div id="contact-link" style={tabCss}>
                <span style={spanCss}>Contact</span>
              </div>
            </Link>
          </ul>
        </nav>

        <Route path="/" exact component={undefined} />
        <Route path="/about/" component={AboutPage} />
        <Route path="/contact/" component={ContactPage} />
      </Router>
    );
  }
}
