import React, { Component, CSSProperties } from 'react';

interface IPageTitleProps {
  title: string;
}

export class PageTitle extends Component<IPageTitleProps> {
  public render() {
    const css: CSSProperties = {
      color: 'black',
    }
    return <h2 style={css}>{this.props.title}</h2>;
  }
}
